.GameButton{
    width: 120px;
    margin: 5px 5px;
}

.GameTitle{
    background-color: rgb(29, 12, 78);
    text-align:center;
    color: white;
    border:3px 
    solid 
  black
}

.CharacterBox{
    width: 200px;
    height: 300px;
    float: left;
}

.ShopBox{
    width: 200px;
    height: 300px;
    float: left;
}

.fightLog{
    width: 250px;
    height: 300px;
    float: left;
    overflow-y:auto;
}

.maxHpBar{
    float:right;
    list-style:none;
    width: 120px;
    height: 10px;
    overflow-x:hidden;
    border: 2px solid rgb(255, 0, 0)
}

.maxMpBar{
    float:right;
    list-style:none;
    width: 120px;
    height: 10px;
    overflow-x:hidden;
    border: 2px solid rgb(0, 68, 255)
}

.maxExpBar{
    float:right;
    list-style:none;
    width: 120px;
    height: 10px;
    border: 2px solid rgb(255, 238, 0)
}

.adventureBox{
    width: 300px;
    height: 300px;
    float: left;
    overflow-y:auto;
}

.adventureBar{
    width: 300px;
    height: 10px;
    float: left;
    overflow-y:auto;
    border: 2px solid rgb(0, 0, 0)
}

.adventureButton{
    margin: 5px 5px;
}




